import { useRef } from "react"
import styles from '../../styles/CanvasGrid.module.css';
import { useEffect } from "react";


export default function CanvasGrid(props) {
  const canvasContainer = useRef(null);

 const iframeRef = useRef(null);

  useEffect(() => {
    const iframe = iframeRef.current;
    if (!iframe) return;

    // focus attempt
    const focusGame = () => {
      try {
        iframe.focus?.();
        // contentWindow.focus may fail silently cross-origin; still try
        iframe.contentWindow?.focus?.();
      } catch (e) {
        // ignore cross-origin errors
      }
      // small retry in case browser needs a moment
      setTimeout(() => {
        try {
          iframe.focus?.();
          iframe.contentWindow?.focus?.();
        } catch (e) {}
      }, 150);
    };

    // Handler for user interaction on iframe
    const onUserInteract = (ev) => {
      // Prevent default only for touchstart if you want to avoid other behaviours
      // ev.preventDefault && ev.preventDefault();
      focusGame();

      // Remove listeners after first interaction to avoid repeated calls
      removeListeners();
    };

    const listeners = [
      ["pointerdown", onUserInteract],
      ["mousedown", onUserInteract],
      ["touchstart", onUserInteract],
      ["click", onUserInteract],
      ["focus", onUserInteract],
      ["mouseenter", onUserInteract],
    ];

    const addListeners = () => {
      listeners.forEach(([evt, handler]) => iframe.addEventListener(evt, handler));
      // Also listen on document in case the iframe gets clicked via overlapped element
      document.addEventListener("pointerdown", docPointerDown);
    };

    const removeListeners = () => {
      listeners.forEach(([evt, handler]) => iframe.removeEventListener(evt, handler));
      document.removeEventListener("pointerdown", docPointerDown);
    };

    // If user clicks somewhere and the event target is the iframe element, focus it
    function docPointerDown(e) {
      if (e.target === iframe || iframe.contains(e.target)) {
        onUserInteract(e);
      }
    }

    addListeners();

    // If iframe is focused via keyboard tab, attach a window keydown fallback to focus it
    const onWindowKeydown = (e) => {
      // If user presses any key while focus is on the page (not input), assume intent to play
      focusGame();
      removeListeners();
      window.removeEventListener("keydown", onWindowKeydown);
    };
    window.addEventListener("keydown", onWindowKeydown);

    // Cleanup
    return () => {
      removeListeners();
      window.removeEventListener("keydown", onWindowKeydown);
    };
  }, []);

  function fullScreen() {
    if (document.fullscreenElement) {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    } else {
      if (canvasContainer.current.requestFullscreen) {
        canvasContainer.current.requestFullscreen();
      } else if (canvasContainer.current.webkitRequestFullscreen) {
        
        canvasContainer.current.webkitRequestFullscreen();
      } else if (canvasContainer.current.mozRequestFullScreen) {
        canvasContainer.current.mozRequestFullScreen();
      } else if (canvasContainer.current.msRequestFullscreen) {
        canvasContainer.current.msRequestFullscreen();
      }
    }
  }

  function Sandbox(value){
    var sand_value;
    if (value == "") {
      // sand_value = "allow-scripts allow-popups allow-same-origin";
    } else {
      // sand_value =
        // "allow-forms allow-pointer-lock allow-popups-to-escape-sandbox allow-same-origin allow-scripts allow-top-navigation-by-user-activation";
    }
    return sand_value;
  }

  return (
    <div className={styles.thumb_canvas_Grid}>
      <div className="canvas-containerr">
        <iframe
          ref={canvasContainer}
          // sandbox={Sandbox(props.brand)}
          className="game-canvas"
          id="game_frame"
          height="600px"
          width="100%"
          frameBorder="0"
          scrolling="no"
          src={props.iframe}
          data-hj-allow-iframe="true"
          allow="autoplay; fullscreen; gamepad"
        ></iframe>
        <div className={styles.game_info}>
          <p className="game-title">{props.name}</p>
          <a className="game-fullscreen" onClick={fullScreen}>
            <img alt="Full screen" title="Full screen" src={props.src}></img>
          </a>
        </div>
      </div>
    </div>
  );
}
